import React, { useState } from 'react';


function App() {
  const [count, setCount] = useState(0);

  return(
    <div className="container">
       <h2>Hello Word!</h2>
       <p>This is my first counter with React</p>
      <h3>CONUTER: {count}</h3>
      <button onClick={() => setCount(count + 1)}>Aumenta</button>
      <button onClick={() => setCount(count - 1)}>Diminuisci</button>
    </div>
  )
}

export default App;
